<template>
	<view class="body">
		<view class="head">
			<view class="head_back"><image src="@/static/img/back.png" mode="" @click="navigateBack()"></image></view>
			<view class="head_center hei_38_bold">提问管理</view>
			<view class=" head_right"><image src="@/static/lsimg/sousuo.png" mode="" @click="tan"></image></view>
		</view>
		<view class="tab_top hui_26">
			
			<text v-for="(item, index) in tab_arry" :class="['', index == active ? 'hong_26_bold' : '']" @click="qiehuan(index)">{{ item }}</text>
		</view>
		<view class="zi_body tab_zi_body">
			<view class="tiwen_list">
				<view class="tiwen_item" v-for="(item, index) in jilu_list" v-if="active!=3">
					<view class="tiwen_item_name">
						<view class="tiwen_item_name_l ">
							<image :src="img_url+item.user.photourl" mode=""></image>
						</view>
						<view class="tiwen_item_name_r">
							<view class="hui_24 tiwen_item_name_phone">
								{{item.user.mobile}}
							</view>
							<view class="hui_24 qian_22">
								{{ item.addtime | timeStamp }}<text style="margin-left: 20rpx;"> {{item.province}}-{{item.city}}</text>
							</view>
						</view>
						<view class="xuanshang_zhuangtai" v-if="item.tiwenstate!=4">
						<text class=" hong_26">{{item.tiwenstate==2?'待悬赏':item.tiwenstate==3?'已悬赏':''}}</text>
							
						</view>
						<view class="xuanshang_zhuangtai" v-if="item.tiwenstate==4">
						<text class=" hong_26">{{item.lawyerid==item.userid?'已悬赏已中奖':'已悬赏未 中奖'}}</text>
							
						</view>
					</view>
					<view class="tiwen_item_top hui_26">
						<text class="zhuangtai" v-if="item.replynum == 0">新</text>
						{{ item.information }}
					</view>
					<!-- <view class="tiwen_item_center qian_22">
						<view class="tiwen_item_dizhi">
							<image src="@/static/img/dizhi.png" mode=""></image>
							{{ item.province }}-{{ item.city }}
						</view>
						<view class="tiwen_item_biaoqian">
							<image src="@/static/img/biaoqian.png" mode=""></image>
							{{ fenlei[item.typeid].typename }}
						</view>
						<view class="tiwen_item_shijian">
							<image src="@/static/lsimg/shijian_icon.png" mode=""></image>
							{{ item.addtime | timeStamp }}
						</view>
					</view> -->
					<!-- <view class="zhuiwen hui_26" v-if="item.zhuiwen">
						
					  <view class="zhuiwen_txt bai_20">追问</view>
					  <view class="zhuiwen_body">{{item.zhuiwen.information}}</view>
					</view> -->
					<view class="huifu_btn">
						<!-- <view class="tiwen_ren hui_24">
							<image :src="img_url+item.user.photourl" mode=""></image>{{item.user.mobile}}
							</view> -->
							<view class="hong_26">悬赏: {{item.ispay==2?item.paymoney:'0'}}元  <text style="margin-left: 20rpx;">回复：{{item.replynum}}</text></view>
						<view class="anniu">
						<button type="" class="huifu hong_24" @click="go_chat(item.userid,item.replynum)" >
							联系
						</button>
						<button type="" class="huifu hong_24" @click="huifu(item.consultid)">
							回复
						</button>
					</view>
					</view>
				</view>
				<view class="" v-for="wei_item in weihui"v-if="active==3">
					
				
				<view class="tiwen_item" v-for="(item, index) in jilu_list" v-if="wei_item.consultid==item.consultid">
					
					<view class="tiwen_item_name">
						<view class="tiwen_item_name_l ">
							<image :src="img_url+item.user.photourl" mode=""></image>
						</view>
						<view class="tiwen_item_name_r">
							<view class="hui_24 tiwen_item_name_phone">
								{{item.user.mobile}}
							</view>
							<view class="hui_24 qian_22">
								{{ item.addtime | timeStamp }}<text style="margin-left: 20rpx;"> {{item.province}}-{{item.city}}</text>
							</view>
						</view>
						<view class="xuanshang_zhuangtai" v-if="item.tiwenstate!=4">
						<text class=" hong_26">{{item.tiwenstate==2?'待悬赏':item.tiwenstate==3?'已悬赏':''}}</text>
							
						</view>
						<view class="xuanshang_zhuangtai" v-if="item.tiwenstate==4">
						<text class=" hong_26">{{item.lawyerid==item.userid?'已悬赏已中奖':'已悬赏未 中奖'}}</text>
							
						</view>
					</view>
					<view class="tiwen_item_top hui_26">
						<text class="zhuangtai" v-if="item.replynum == 0">新</text>
						{{ item.information }}
					</view>
					
					<view class="huifu_btn">
							<view class="hong_26">悬赏: {{item.ispay==2?item.paymoney:'0'}}元  <text style="margin-left: 20rpx;">回复：{{item.replynum}}</text></view>
							
						<view class="anniu">
							
						
						<button type="" class="huifu hong_24" @click="go_chat(item.userid)">
							联系
						</button>
						<button type="" class="huifu hong_24" @click="huifu(item.consultid)">
							回复
						</button>
						</view>	
					</view>
					
				</view>
				
			</view>	
				
				
			</view>
			
			<view class="none_list" v-if="jilu_list.length==0">
							<image src="../../static/img/none_tiwen.png" mode="widthFix"></image>
							<view class="none_list_txt qian_26">
						             暂无提问记录
							</view>
		    </view>
			
			
			
		</view>

		<uni-popup type="bottom" ref="popup">
			<view class="tan">
				<view class="tan_title hei_30">查询条件</view>
				<view class="tan_list hei_26">
					<view class="tan_list_top">
						<pickerAddress @change="change" class="aaaaaa" @click="dianji">
							<text class="dizhi">{{ dizhi == '' ? '按地区' : dizhi }}</text>
						</pickerAddress>
						<!-- <text>按地区</text> -->
						<image src="@/static/lsimg/xuan_xia.png" mode=""></image>
					</view>
				</view>
				<view class="tan_list hei_26">
					<view class="tan_list_top" @click="zhuan_togg">
						<text>{{ zhuanchang_txt == '' ? '按专长' : zhuanchang_txt }}</text>
						<image src="@/static/lsimg/xuan_xia.png" mode=""></image>
					</view>
					<view class="tan_list_bottom zhuanchang_arry hei_26" v-if="zhuan_show">
						<text v-for="(item, index) in fenlei" :class="['', index == xuanzc ? 'xuanzhong' : '']" @click="xuanzhuangchang(index, item.typename, item.typeid)">
							{{ item.typename }}
						</text>
					</view>
					<button type="" class="zhuanchang_quer_ding bai_30" @click="zhuanchang_go" v-if="zhuan_show">确定</button>
				</view>

				<view class="tan_buttom " v-if="!zhuan_show">
					<button type="" class="quxiao hong_30" @click="quxiao_btn">取消搜索</button>
					<button type="" class="queren bai_30" @click="queding_btn">确认搜索</button>
				</view>
			</view>
		</uni-popup>
		
	<view class="bg" v-if="is_tan">
		<view class="tan_tishi" >
			<view class="tan_tishi_top hei_30_bold">
				<view class="tan_tishi_top_left hong_30_bold">
			       <image src="@/static/lsimg/jinggao.png" mode=""></image>	<text>提示</text>
					</view>
				 <image src="@/static/img/tan_close.png" mode="" @click="close" class="guanbi"></image>
			</view>
			<view class="hui_24 tan_tishi_txt">
				 请先解答用户后才可以联系用户和显示电话号码！
			</view>
			<button type="" class="bai_26" @click="close">知道啦</button>
		</view>
	</view>		
		
		
	</view>
</template>

<script>
import unpopup from '@/components/uni-popup/uni-popup-share.vue';
import pickerAddress from '@/components/pickerAddress_buxian/wangding-pickerAddress.vue';
export default {
	components: {
		unpopup,
		pickerAddress
	},
	onLoad(option) {
		// 获取分类
		this.$http
			.post({
				url: '/mapi/index/gettype'
			})
			.then(res => {
				this.fenlei = res.data.type;
				
				this.huoqu_list();
				
			});
			this.$http
				.post({
					url: '/mlawyerapi/consult/zaixian_lianxi'
				})
				.then(res => {
					this.is_lianxi=res.data.lawyer.zaixian_lianxi
				});	
		// this.huoqu_dihzi()	
	this.$http
		.post({
			url: '/mlawyerapi/lawyer/auth',
			data: {
				type:2
			}
		})
		.then(res => {
			this.lsdizhi=
			{
				province:res.data.lawyerauth.province,
				city:res.data.lawyerauth.city,
				area:res.data.lawyerauth.area,
			}
			this.state=option.state
			if(this.state){
				this.qiehuan(1)
			}
			
		});

	
	
	},
	onShow() {
		// this.zhuanchang_txt = '';
		// this.dizhi = '';
		// this.type_id = '';
		// this.sheng = '';
		// this.shi = '';
		// this.xuanzc = '9999';
		// this.page = 0;
		// this.jilu_list = [];
		// this.weihui=[]
		// this.is_all = false;
		// this.huoqu_list();
		
			
		
	},

	data() {
		return {
			tab_arry: ['全部咨询','当地咨询', '我回复的', '追问我的'],
			active: '0',
			dizhi: '',
			zhuanchang: [],
			xuanzc: '9999',
			zhuanchang_txt: '',
			zhuanchang_txt2: '',
			zhuan_show: false,
			jilu_list: [],
			page: 0,
			is_all: false,
			img_url: uni.getStorageSync('img_url'),
			fenlei: '',
			type_id: '',
			sheng: '',
			shi: '',
			state:1,
			lsdizhi:'',
			weihui:[],
			is_lianxi:2,
			is_tan:false,
		};
	},
	created() {},
	//下拉刷新
	onPullDownRefresh: function() {
		this.page = 0;
		this.jilu_list = [];
		this.weihui=[]
		this.is_all = false;
		this.zhuanchang_txt = '';
		this.dizhi = '';
		this.sheng = '';
		this.shi = '';
		this.type_id = '';
		this.xuanzc = '9999';
		this.huoqu_list();
	},
	// stopPullDownRefresh:function(){

	// },
	methods: {
		//上拉加载
		onReachBottom() {
			if (this.is_all) {
				uni.showToast({
					title: '没有更多内容了',
					duration: 2000,
					icon: 'none'
				});
				uni.stopPullDownRefresh();
				return false;
			} else {
				this.page++;
				this.huoqu_list();
			}
		},
		navigateBack() {
			uni.navigateBack();
		},
		go_chat(userid,replynum){
			
		if(this.is_lianxi==1){
			uni.navigateTo({
				url: 'chat?userid=' + userid
			});
		}else{
			if(replynum==0){
			
				this.is_tan=true
			}else{
				uni.navigateTo({
					url: 'chat?userid=' + userid
				});
			}
		}
			
			
			
			
			
		},
		close(){
			this.is_tan=false
		},
		// 切换tab
		qiehuan(index) {
			this.active = index;
			this.zhuanchang_txt = '';
			this.dizhi = '';
			this.type_id = '';
			this.sheng = '';
			this.shi = '';
			this.xuanzc = '9999';
			this.page = 0;
			this.jilu_list = [];
			this.weihui=[]
			this.is_all = false;
			if(this.active==0){
				this.state=1
			}else if(this.active==1){
				this.state=4
				
				this.sheng=this.lsdizhi.province
				this.shi=this.lsdizhi.city
			}else if(this.active==2){
				this.state=2
			}
			else if(this.active==3){
				this.state=3
			}
			this.huoqu_list();

		},
		huoqu_dihzi(){
			this.$http
				.post({
					url: '/mlawyerapi/lawyer/auth',
					data: {
						type:2
					}
				})
				.then(res => {
					this.lsdizhi=
					{
						province:res.data.lawyerauth.province,
						city:res.data.lawyerauth.city,
						area:res.data.lawyerauth.area,
					}
					
					
				});
		},
		// 获取提问记录列表
		huoqu_list() {
			
			
			if(this.sheng=='不限地区'){
				this.sheng=''
                 this.shi=''
			}
			
			this.$http
				.post({
					url: '/mlawyerapi/consult/tiwen_guanli',
					data: {
						page: this.page,
						state: this.state,
						typeid: this.type_id,
						province: this.sheng,
						city: this.shi
					}
				})
				.then(res => {
					this.jilu_list = this.jilu_list.concat(res.data.list);
					this.weihui=this.weihui.concat(res.data.weihuifu)
					this.tab_arry[3]='追问我的'+'('+res.data.weihuifu.length+')'
					if (res.data.list.length < 10) {
						this.is_all = true;
					}
				});
		},
		huifu(id) {
			// if(this.active==2){
				uni.navigateTo({
					url:
						'/pages/ls/zhuiwen_xq?id='+id
				});
			// }else{
			// 	uni.navigateTo({
			// 		url:
			// 			'/pages/ls/tiwen_guanli_xq?id='+id
			// 	});
			// }
		
		},
		// 底部弹窗
		tan() {
			this.$refs.popup.open();
		},
		// 地址确定
		change(data) {
			console.log(data);
			this.sheng = data.data[0];
			this.shi = data.data[1];
			this.dizhi = this.sheng + '-' + this.shi;
			this.zhuan_show = false;
		},
		// 专长选择
		xuanzhuangchang(index, item, id) {
			this.xuanzc = index;
			this.zhuanchang_txt2 = item;
			this.type_id = id;
		},
		// 专长确定
		zhuanchang_go() {
			this.zhuanchang_txt = this.zhuanchang_txt2;
			this.zhuan_show = !this.zhuan_show;
		},
		zhuan_togg() {
			this.zhuan_show = !this.zhuan_show;
		},
		// 取消搜索
		quxiao_btn() {
			this.zhuanchang_txt = '';
			this.dizhi = '';
			this.type_id = '';
			this.sheng = '';
			this.shi = '';
			this.xuanzc = '9999';
			this.$refs.popup.close();
		},
		// 确定搜索
		queding_btn() {
			console.log(this.dizhi, this.zhuanchang_txt);
			this.$refs.popup.close();
			this.page = 0;
			this.jilu_list = [];
			this.is_all = false;
			this.huoqu_list();
		},
		tui_xq(item){
			uni.navigateTo({
				url:'tui_xq?item='+JSON.stringify(item)+'&type=3'
			})
		},
	},
	filters: {
		timeStamp: function(value) {
			if (value == null) {
				return 'null';
			}
			var i = (value + '').length;
			while (i++ < 13) value = value + '0';
			value = Number(value);
			var date = new Date(value);
			//date.setTime(value);
			var month = date.getMonth() + 1;
			var hours = date.getHours();
			if (hours < 10) hours = '0' + hours;
			var minutes = date.getMinutes();
			if (minutes < 10) minutes = '0' + minutes;
			var time = date.getFullYear() + '-' + month + '-' + date.getDate() + ' ' + hours + ':' + minutes;
			return time;
		}
	}
};
</script>

<style>
page {
	/* background-color: #f7f7f7; */
}

.tab_top {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 10rpx 38rpx;
	background-color: #ffffff;
	position: fixed;
	top: 100rpx;
	width: 100%;
	box-sizing: border-box;
	z-index: 9;
}

.head_right image {
	width: 40rpx;
	height: 41rpx;
}

.huifu {
	width: 140rpx;
	height: 46rpx;
	background-color: #ffffff;
	border-radius: 28rpx;
	border: solid 2rpx #f43a51;
	border-radius: 28rpx;
	line-height: 46rpx;
	margin: 0;
	display: inline-block;
	text-align: center;
}
.huifu:first-child{
	margin-right: 20rpx;
}
.tiwen_item {
	padding: 20rpx 30rpx 0;
	margin-bottom: 20rpx;
	background-color: #ffffff;
	border-top: 20rpx solid #F7F7F7;
}
.tiwen_item:last-child{
	border-bottom: 20rpx solid #F7F7F7;
}

.tiwen_item:first-child {
	margin-top: 20rpx;
}

.tiwen_item_center {
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin: 20rpx 0;
}

.tiwen_item_dizhi image {
	width: 18rpx;
	height: 20rpx;
	margin-right: 8rpx;
}

.tiwen_item_biaoqian image {
	width: 21rpx;
	height: 20rpx;
	margin-right: 8rpx;
}

.tiwen_item_shijian image {
	width: 21rpx;
	height: 21rpx;
	margin-right: 8rpx;
}

.huifu_btn {
	display: flex;
	align-items: center;
	justify-content: space-between;
	border-top: 2rpx dashed #c6c6c6;
	height: 105rpx;
}
.huifu_btn image {
	width: 50rpx;
	height: 51rpx;
	margin-right: 15rpx;
	border-radius: 100%;
}
.zhuangtai {
	background-color: #f43a51;
	border-radius: 3rpx;
	color: #ffffff;
	padding: 0 5rpx;
	display: inline-block;
	border-radius: 4rpx;
	font-size: 20rpx;
	vertical-align: middle;
	margin-right: 9rpx;
	
}
.tiwen_ren {
	display: flex;
	align-items: center;
}

.tan {
	width: 100%;
	height: 758rpx;
	background-color: #ffffff;
	padding: 0 23rpx;
	box-sizing: border-box;
	position: relative;
}

.tan_title {
	text-align: center;
	line-height: 120rpx;
}

.tan_list {
}

.tan_list_top {
	display: flex;
	align-items: center;
	border-bottom: 2rpx solid #e8e8e8;
	height: 98rpx;
	justify-content: space-between;
}

.tan_list image {
	width: 22rpx;
	height: 12rpx;
}

.tan_buttom {
	display: flex;
	align-items: center;
	position: absolute;
	bottom: 120rpx;
}

.tiwen_item_top {
	line-height: 36rpx;
	word-break: break-all;
	margin: 8rpx 0 18rpx;
	display: -webkit-box;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 3;
	overflow: hidden;
	
}

.aaaaaa {
	min-width: 650rpx;
	display: inline-block;
}

button {
	margin: 0;
	border: none;
}

button::after {
	content: none;
}

.quxiao {
	width: 342rpx;
	height: 88rpx;
	background-color: #ffd7dc;
	border-radius: 44rpx 0rpx 0rpx 44rpx;
	line-height: 88rpx;
}

.queren {
	width: 342rpx;
	height: 88rpx;
	line-height: 88rpx;
	background-color: #f43a51;
	border-radius: 0rpx 44rpx 44rpx 0rpx;
}

.zhuanchang_arry {
	display: flex;
	margin-top: 20rpx;
	flex-wrap: wrap;
}

.zhuanchang_arry text {
	border: 2rpx solid #e8e8e8;
	border-radius: 20rpx;
	padding: 4rpx 20rpx;
	display: inline-block;
	margin-right: 28rpx;
	margin-bottom: 20rpx;
}

.zhuanchang_quer_ding {
	height: 88rpx;
	background-color: #f43a51;
	border-radius: 44rpx;
	line-height: 88rpx;
	position: absolute;
	bottom: 50rpx;
	width: 691rpx;
	box-sizing: border-box;
}

.xuanzhong {
	color: #f43a51;
	border: 2rpx solid #f43a51 !important;
}

.zhuiwen {
	display: flex;
	align-items: center;
	margin-top: 20rpx;
	
	border-top: 2rpx dashed #c6c6c6;
	height: 68rpx;

}
.zhuiwen .zhuiwen_txt {
	padding: 2rpx 4rpx;
	height: 26rpx;
	background-color: #f43a51;
	border-radius: 3rpx;
	margin-right: 7rpx;
	line-height: 26rpx;
	margin-top: 6rpx;
}
.zhuiwen_body {
	max-width: 86%;
	line-height: 38rpx;
	overflow:hidden;
	text-overflow:ellipsis;
	white-space:nowrap; 
}
.tiwen_item_name_l{
	margin-right: 13rpx;
}
.tiwen_item_name_l image{
		width: 66rpx;
		height: 66rpx;
		border-radius: 100%;
}
.tiwen_item_name{
	display: flex;
	align-items: center;
	position: relative;
	
}
